<template>
  <div class="layout-wrapper">
    <navigator section="edit"></navigator>
    <div class="edit-page">
      <div class="main-content">
        <div class="paper-wrapper">
          <paper-header
            v-bind:title="paper.title"
            v-bind:description="paper.description"
            v-on:paperHeaderChange="paperHeaderChange"
          >
          </paper-header>
          <div
            class="topic-wrapper"
            v-for="(topic, index) in paper.topics"
          >
            <edit-topic
              v-if="topicEditingID === topic.id"
              v-bind:topic="topic"
              v-on:savePaperTopic="savePaperTopic"
              v-on:cancelTopicEditing="cancelTopicEditing"
            >
            </edit-topic>
            <single-choice
              v-else
              v-bind:topic="topic"
              v-bind:topicIndex="index"
              v-on:switchToEdit="switchToEdit"
            >
            </single-choice>
            <span class="delete-topic" v-on:click="deletePaperTopic(topic.id)">&times;</span>
          </div>
          <div class="add-wrapper" v-if="topicAdding">
            <edit-topic
              v-on:savePaperTopic="savePaperTopic"
              v-on:cancelTopicEditing="cancelTopicEditing"
            >
            </edit-topic>
          </div>
          <add-topic-button v-on:addPaperTopic="addPaperTopic"></add-topic-button>
        </div>
      </div>
    </div>
    </div>
</template>

<script>
import _ from 'lodash'
import io from 'socket.io-client'
import { remotePath } from '@/constants/RemotePath.js'
import Navigator from '@/layouts/commons/Navigator.vue'
import EditTopic from '@/components/papers/EditTopic.vue'
import AddTopicButton from '@/components/papers/AddTopicButton.vue'
import PaperHeader from '@/components/papers/PaperHeader.vue'
import SingleChoice from '@/components/papers/SingleChoice.vue'
import { newPaperDefault } from '@/constants/Topics.js'
export default {
  name: 'edit',
  components: {
    Navigator,
    EditTopic,
    AddTopicButton,
    PaperHeader,
    SingleChoice
  },
  data () {
    return {
      socket: io.connect(`${remotePath.socketHost}`, {forceNew: true}),
      id: this.$route.params.id,
      topicAdding: false,
      topicEditingID: null,
      paper: {
        title: newPaperDefault.title,
        description: newPaperDefault.description,
        topics: []
      }
    }
  },
  created () {
  },
  mounted () {
    if (this.id) {
      this.isNew = false
      this.fetchPaper()
    } else {
      this.isNew = true
    }
    this.socket.on('updateSuccess', (res) => {
      console.log('received updated message:', res)
      if (res.data.status) {
        this.paper = res.data.paper
      }
    })
  },
  methods: {
    getpaperJSON () {
      const paperCp = _.cloneDeep(this.paper)
      return JSON.stringify(paperCp)
    },
    fetchPaper () {
      const url = `${remotePath.path}/paper/getPaper?id=${this.id}`
      fetch(url, {
        method: 'get',
        mode: 'cors'
      }).then((res) => {
        return res.json()
      }).then((paperData) => {
        this.paper = paperData.data.paper
      }).catch((err) => {
        alert(err)
      })
    },
    addPaperTopic () {
      this.topicAdding = true
    },
    switchToEdit (id) {
      this.topicEditingID = id
    },
    savePaperTopic (topic) {
      if (this.topicAdding) {
        this.paper.topics.push(topic)
      } else {
        let topicIndex
        this.paper.topics.filter((item, index) => {
          if (item.id === topic.id) {
            topicIndex = index
            return true
          }
        })
        this.paper.topics.splice(topicIndex, 1, topic)
      }
      this.savePaper()
    },
    deletePaperTopic (topic) {
      let topicIndex
      this.paper.topics.filter((item, index) => {
        if (item.id === topic.id) {
          topicIndex = index
          return true
        }
      })
      this.paper.topics.splice(topicIndex, 1)
      this.savePaper()
    },
    cancelTopicEditing () {
      console.log('cancel')
      this.topicAdding = false
      this.topicEditingID = null
    },
    paperHeaderChange (title, description) {
      this.paper.title = title
      this.paper.description = description
      this.savePaper()
    },
    createPaper () {
      return fetch(`${remotePath.path}/paper/createPaper`, {
        method: 'post',
        mode: 'cors'
      })
    },
    updatePaper (paperJSON) {
      console.log(paperJSON)
      fetch(`${remotePath.path}/paper/updatePaper`, {
        method: 'post',
        headers: {
          'Content-Type': 'application/json'
        },
        mode: 'cors',
        body: paperJSON
      }).then((updateRes) => {
        return updateRes.json()
      }).then((updateData) => {
        this.topicAdding = false
        this.topicEditingID = null
        this.socket.emit('updatePaper', {id: this.id})
      }).catch((err) => {
        alert(err)
      })
    },
    savePaper () {
      if (this.isNew) {
        this.createPaper().then((res) => {
          return res.json()
        }).then((respData) => {
          console.log(this)
          this.isNew = false
          this.paper.id = respData.data.id
          const paperJSON = this.getpaperJSON()
          this.updatePaper(paperJSON)
        })
      } else {
        const paperJSON = this.getpaperJSON()
        this.updatePaper(paperJSON)
      }
    }
  }
}
</script>

<style scoped lang="scss">
  .topic-wrapper {
    position: relative;
    padding: 30px 0;
    border-bottom: 1px solid #eee;
    .delete-topic {
      position: absolute;
      top: 0;
      right: 0;
      display: none;
      font-size: 24px;
      color: #333;
      cursor: pointer;
    }
    &:hover {
      .delete-topic {
        display: block;
      }
    }
  }
</style>



