<template>
  <div class="paper-header">
    <input
      type="text"
      required
      class="paper-title"
      v-model="paperTitle"
      @change.stop.prevent="paperHeaderChange"
    />
    <textarea
      type="text"
      class="paper-description"
      v-model="paperDescription"
      @change.stop.prevent="paperHeaderChange"
    >
    </textarea>
  </div>
</template>

<script>
  export default {
    name: 'paper-header',
    props: {
      title: String,
      description: String
    },
    data () {
      return {
        paperTitle: this.title,
        paperDescription: this.description
      }
    },
    watch: {
      title: function (newTitle) {
        this.paperTitle = newTitle
      },
      description: function (newDescription) {
        this.paperDescription = newDescription
      }
    },
    // computed: {
    //   editingTitle: {
    //     get: function () {
    //       return this.paperTitle.trim()
    //     },
    //     set: function (newTitle) {
    //       this.paperTitle = newTitle
    //     }
    //   },
    //   editingDescription: function () {
    //     return this.description.length ? this.description.trim() : ''
    //   }
    // },
    methods: {
      paperHeaderChange () {
        this.$emit('paperHeaderChange', this.paperTitle, this.paperDescription)
      }
    }
  }
</script>

<style scoped lang="scss">
  .paper-title {
    margin: 20px 0;
    width: 100%;
    font-size: 24px;
    line-height: 1.4;
    text-align: center;
    border: none;
    &:hover{
      box-shadow: 0 1px 2px rgba(0,0,0,0.07);
    }
  }
  .paper-description {
    margin: 20px 0;
    padding: 10px 0;
    width: 100%;
    max-width: 100%;
    min-height: 100px;
    font-size: 14px;
    line-height: 1.8;
    border: none;
    resize: none;
    box-sizing:border-box;
    &:hover{
      box-shadow: 0 1px 2px rgba(0,0,0,0.07);
    }
  }
</style>



